.navbar {
    width: 100%;
    height: 30px;
    background-color: #00B25D;
}

.navbar a {
    text-decoration: none;
    color: white;
}

.navbar li {
    list-style-type: none;
    /* display: inline; */
    padding: 5px 10px;
    

}

.navbar a:hover {
    color: #f08d3c;
}
.right {
    float: right;
}

.left {
    float: left;
}


